@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap');

$color-light: white;
$color-dark: #301566;
$primary-color: #00A015;
$secondary-color: #EEEEEE;
$tertiary-color: #C8C8C8;


* {
    margin: 0;
    padding: 0;
    border: 0;
    line-height: 1.4em;
    vertical-align: baseline;
    text-decoration: none;
    outline: 0;
    box-sizing: border-box;
    background-color: transparent;
    font-family: 'Poppins', sans-serif;
}

.color-light {
    color: $color-light;
}

.color-dark {
    color: $color-dark;
}

h1 {
    font-family: 'Poppins', sans-serif;
    font-size: 80px;
    font-weight: 600;
    line-height: 40px;
    margin-bottom: 20px;
}


h2 {
    font-family: 'Poppins', sans-serif;
    font-size: 45px;
    font-weight: 800;
    line-height: 54px;
    margin-bottom: 30px;
}

h3 {
    font-family: 'Montserrat', sans-serif;
    font-size: 25px;
    font-weight: 800;
    line-height: 40px;
    margin-bottom: 30px;
}
   
h4 {
    font-family: 'Montserrat', sans-serif;
    font-size: 20px;
    font-weight: 800;
    line-height: 24px;
}

p {
    font-family: 'Poppins', sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 26px;
    margin-bottom: 50px;
}

hr {
    border-top: 2px solid $color-dark;
    margin: 10px 0 30px 0;
}

a.button {
    padding: 20px 40px;
    background-color: $primary-color;
    margin: 0 auto;
    border-radius: 50vh;
    font-family: 'Poppins', sans-serif;
    font-size: 16px;
    font-weight: 600; 
    line-height: 15px;
    &:hover {
        opacity: 0.9;
    }
    &.to-gray:hover {
        border: solid white 2px;
        background-color: transparent;
    }
}



.wrapper {
    display: flex; 
    flex-direction: column;
    min-height: 100vh;
}

header {
    padding: 20px 20px;
    .container {
        max-width: 1170px;
        display: flex;
        justify-content: space-between;
        margin: 0 auto;
        .logo {
            display: flex;
        }
        nav {
            display: flex;
            align-items: center;
            ul {
                display: flex;
                li {
                    margin: 0 10px;
                    list-style: none;
                    a {
                        font-size: 16px;
                        font-weight: 500;
                        color: $color-dark;
                    }
                }
                
            }
        }
    }
}

.hero {
    position: relative;
    .background-image {
        background: url("../img/1.jpg") no-repeat 58% 75%;
      
        background-size: cover;
        position: absolute;
        height: 100%;
        width: 100%;
        filter: brightness(80%) grayscale(20%);
        z-index: -1;
    }
    .container {
        padding: 110px 40px 90px 40px;
        display: flex;
        flex-direction: column;
        text-align: center;
        max-width: 900px;
        margin: 0 auto;
    }
}

.text {
    padding: 100px 20px 0 20px;
    .container {
        display: flex;
        flex-direction: column;
        text-align: center;
        max-width: 725px;
        margin: 0 auto;
        padding: 20px 20px 0 20px;
        h2 {
            margin-bottom: 20px;
        }
        p {
            margin-bottom: 30px;
        }
    }
}

.gallery {
    display: flex;
    gap: 7px;
    img {
        width: 33.33333%
    }
}

.blocks {
    background-color: $secondary-color;
    padding: 40px 20px 50px 20px;
    .container {
        max-width: 1050px;
        display: flex;
        justify-content: center;
        margin: 0 auto;
        .block {
            padding: 40px 20px;
            background-color: $color-light;
            width: 33.33333%;
            display: flex;
            flex-direction: column;
            align-items: center;
            .icon {
                height: 88px;
                width: 88px;
                display: flex;
                justify-content: center;
                align-items: center;
                margin-bottom: 30px;
                img {
                    height: 55px;
                }
            }
            h4 {
                text-align: center;
                margin-bottom: 20px;
            }
            p {
                text-align: center;
                margin-bottom: 30px;
            }
        }
    }
} 

.img-right {
    padding: 60px 20px;
    .container {
        display: flex;
        margin: 20px;
        max-width: 1170px;
        margin: 20px auto;
        .content {
            width: 50%;
            padding: 20px 60px 20px 40px;
            h2 {
                margin-bottom: 20px;
            }
            p {
                text-align: left;
                margin-bottom: 30px;
            }
            .buttons {
                margin-top: 40px;
                display: flex;
                justify-content: flex-start;
                gap: 20px;
                a {
                    margin: 0;
                }
            }
        }
        .image {
            height: 620px;
            width: 50%;
            img {
                object-fit: cover;
                width: 100%;
                height: 100%;
                
                
            }
        }
    }
}

.counters {
    padding: 80px 20px 40px 20px;
    background-color: $primary-color;
    .container {
        max-width: 1100px;
        margin: 0 auto;
        display: flex;
        
        .counter {
            text-align: center;
            color: $color-light;
            width: 33.33333%;
            h4 {
                margin-bottom: 20px;
            }
        }
    }
}

.img-left {
    padding: 60px 20px;
    .container {
        display: flex;
        margin: 20px;
        max-width: 1100px;
        margin: 20px auto;
        .content {
            width: 50%;
            padding: 20px 40px;
            h2 {
                margin-bottom: 20px;
            }
            p {
                text-align: left;
                margin-bottom: 30px;
            }
            .buttons {
                display: flex;
                justify-content: flex-start;
                gap: 20px;
                a {
                    margin: 0;
                }
            }
        }
        .image {
            height: 620px;
            width: 50%;
            img {
                object-fit: cover;
                width: 100%;
                height: 100%;
                
                
            }
        }
    }
}

.map {
    background-color: $secondary-color;
    padding: 80px 40px;
    .container {
        display: flex;
        margin: 20px;
        max-width: 1100px;
        margin: 20px auto;
        .content {
            background-color: $color-light;
            width: 50%;
            padding: 20px 40px;
            h3 {
                margin-bottom: 20px;
            }
            p {
                text-align: left;
                margin-bottom: 30px;
            }
            hr {
                border-width: 4px;
                border-color: $secondary-color;
                &.top {
                    margin: 10px 0 20px 0;
                }
                &.bottom {
                    margin: 20px 0;
                }
            }
            .buttons {
                display: flex;
                justify-content: center;
                gap: 20px;
                a {
                    margin: 0;
                    padding: 0;
                    border-radius: 50%;
                    height: 40px;
                    width: 40px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    img {
                        height: 20px;
                    }
                }
            }
            .address {
                display: flex;
                justify-content: center;
                a {
                    display: flex;
                    align-items: center;
                    gap: 15px;
                    padding-top: 5px;
                    color: $color-dark;
                    img {
                        height: 16px;
                    }
                    p {
                        
                        font-weight: 600;
                        margin: 0;
                    }
                }
            }
        }
        .image {
            width: 50%;
            img {
                object-fit: cover;
                width: 100%;
                height: 100%;
            }
        }
    }
}

.contact {
    background-color: $tertiary-color;
    padding: 60px 20px;
    .container {
        display: flex;
        margin: 20px;
        max-width: 1100px;
        margin: 20px auto;
        .content {
            width: 50%;
            padding: 20px 40px;
            h2 {
                margin-bottom: 20px;
            }
            p {
                text-align: left;
                margin-bottom: 30px;
                font-size: 21px;
            }
            
        }
        .buttons {
            width: 50%;
            padding: 20px 40px;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            a.button {
                font-size: 20px;
                padding: 25px 135px;
                margin: 0;
            }
        }
        .image {
            height: 620px;
            width: 50%;
            img {
                object-fit: cover;
                width: 100%;
                height: 100%;
                
                
            }
        }
    }
}

.footer-top {
    padding: 20px;
    .container {
        max-width: 1170px;
        display: flex;
        justify-content: center;
        margin: 0 auto;
        .block {
            padding: 20px;
            width: 33.33333%;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            h4 {
                text-align: center;
                margin-bottom: 20px;
            }
            p {
                text-align: left;
                margin-bottom: 0;
            }
            
            a {
                font-weight: 700;
            }

        }
        .block:first-child {
            a {
                font-weight: 400;
                color: $color-dark;
            }
        }
        .block:nth-of-type(2) {
            p {
                margin-bottom: 40px;
            }
            img {
                width: 150px;
            }
        }
        .block:last-child {
            p:last-of-type {
                margin-bottom: 20px;
            }
        }
    }
}

.footer-bottom {
    background-color: $tertiary-color;
    padding: 15px 0;
    .container {
        max-width: 1170px;
        display: flex;
        justify-content: flex-start;
        margin: 0 auto;
        font-weight: 500;
        color: $color-light;
    }
}

// about

