    content: "";
    background-color: $tertiary-color;
    position: absolute;
    top: 0px;
    right: 0px;
    bottom: 0px;
    left: 0px;
    z-index: -1;
}

.hero.about {
    position: relative;
    height: 100%;
    width: 100%;
    .background-image {
        position: absolute;
        top: 0px;
        right: 0px;
        bottom: 0px;
        left: 0px;
        opacity: 0.1;
        background: url("../img/11.jpg") no-repeat 0% 50%;
        background-size: cover;
        z-index: -1;
    }
    .container {
        padding: 70px 20px 90px 20px;
        display: flex;
        flex-direction: column;
        max-width: 1140px;
        margin: 0 auto;
        z-index: 0;
        h1 {
            margin-top: 45px;
            margin-bottom: 45px;
            font-size: 65px;
            font-weight: 800;
            font-family: 'Montserrat', sans-serif;
            text-align: left;
        }
    }
}

.img-right.about {
    padding: 20px;
    .container {
        .content {
            display: flex;
            flex-direction: column;
            justify-content: center;
            h2 {
                font-family: 'Montserrat', sans-serif;
            }
        }
        .image {
            img {
                object-fit: contain;
            }
        }
    }
}

.img-left.about {
    padding: 20px;
    .container {
        .content {
            .subtitle {
                margin-bottom: 10px;
                font-size: 16px;
                font-weight: 600;
                color: $color-dark;
            }
            display: flex;
            flex-direction: column;
            justify-content: center;
            h2 {
                font-family: 'Montserrat', sans-serif;
