            img {
                object-fit: cover;
                object-position: 0 75%;
            }
        }
    }
}

.boxes-section {
    color: $color-dark;
    .container {
        max-width: 1170px;
        display: flex;
        flex-direction: column;
        margin: 0 auto;
        padding: 40px;
        h2 {

        }
        p {

        }
        .boxes {
            display: flex;
            flex-wrap: wrap;
            margin-bottom: 60px;
            .box {
                width: 33.33333%;
                padding: 20px;
                h3 {

                }
                p {
                    margin: 0;
                }
            }
        }
