// contact

.hero.contact::before {
    content: "";
    background-color: $tertiary-color;
    position: absolute;
    top: 0px;
    right: 0px;
    bottom: 0px;
    left: 0px;
    z-index: -1;
}

.hero.contact {
    position: relative;
    height: 100%;
    width: 100%;
    .background-image {
        position: absolute;
        top: 0px;
        right: 0px;
        bottom: 0px;
        left: 0px;
        opacity: 0.1;
        background: url("../img/19.jpg") no-repeat 0% 50%;
        background-size: cover;
        z-index: 0;
    }
    .container {
        padding: 70px 20px 90px 20px;
        display: flex;
        flex-direction: column;
        max-width: 1140px;
        margin: 0 auto;
        h1 {
            margin-top: 45px;
            margin-bottom: 45px;
            font-size: 65px;
            font-weight: 800;
            font-family: 'Montserrat', sans-serif;
            text-align: left;
            z-index: 1;
        }
    }
}

.text-boxes {
    
    .container {
        display: flex;
        max-width: 1100px;
        margin: 20px auto;
        color: $color-dark;
        .text-box {
            width: 50%;
            padding: 20px;
            margin: 20px 0;
            h2 {
                margin-bottom: 20px;
            }
            h3 {
                margin-bottom: 30px;
            }
            p {
                margin-bottom: 30px;
            }
            a {
                color: $color-dark;
            }
            &.right {
                text-align: right;
            }
        }
        .contact-form {
            width: 50%;
            padding: 40px;
            form {
                display: flex;
                flex-direction: column;
                
                label {
                    margin: 10px 0 4px 0;
                    font-weight: 700;
                    span {
                        color: red;
                        font-weight: 400;
                    }
                }

                input {
                    max-width: 70%;
                    height: 38px;
                    margin: 10px 0;
                }
                input:focus, textarea:focus {
                    border: 1px solid #999;
                }

                input[type="checkbox"] {
                    max-width:none;
                    height: initial;
                    display: inline;
                    margin: 0;
                    margin-right: 3px;
                }

                input[type="submit"] {
                    max-width: min-content;
                    height: initial;
                    display: inline;
                    background-color: $secondary-color;
                    padding: 10px 15px;
                    font-weight: 700;
                    font-size: 16px;
                }

                textarea {
                    height: 120px;
                }

                input, textarea {
                    border: 1px solid #ccc;
                    padding: 6px 10px;
                    margin-bottom: 10px;
                    border-radius: 2px;
                    
                }

                .checkboxes {
                    margin-bottom: 10px;
                    label {
                        font-weight: 400;
                    }
                }
            }
        }
    }
}

.map-boxes {
    background-color: $secondary-color;
    .container {
        display: flex;
        max-width: 1100px;
        padding: 60px 20px 80px 20px;
        margin: 0 auto;
       
        
        .map-box {
          
            width: 100%;
        }
    }
}
