@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap");
* {
  margin: 0;
  padding: 0;
  border: 0;
  line-height: 1.4em;
  vertical-align: baseline;
  text-decoration: none;
  outline: 0;
  box-sizing: border-box;
  background-color: transparent;
  font-family: "Poppins", sans-serif;
}

.color-light {
  color: white;
}

.color-dark {
  color: #301566;
}

h1 {
  font-family: "Poppins", sans-serif;
  font-size: 80px;
  font-weight: 600;
  line-height: 40px;
  margin-bottom: 20px;
}

h2 {
  font-family: "Poppins", sans-serif;
  font-size: 45px;
  font-weight: 800;
  line-height: 54px;
  margin-bottom: 30px;
}

h3 {
  font-family: "Montserrat", sans-serif;
  font-size: 25px;
  font-weight: 800;
  line-height: 40px;
  margin-bottom: 30px;
}

h4 {
  font-family: "Montserrat", sans-serif;
  font-size: 20px;
  font-weight: 800;
  line-height: 24px;
}

p {
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
  margin-bottom: 50px;
}

hr {
  border-top: 2px solid #301566;
  margin: 10px 0 30px 0;
}

a.button {
  padding: 20px 40px;
  background-color: #00A015;
  margin: 0 auto;
  border-radius: 50vh;
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  font-weight: 600;
  line-height: 15px;
}
a.button:hover {
  opacity: 0.9;
}
a.button.to-gray:hover {
  border: solid white 2px;
  background-color: transparent;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

header {
  padding: 20px 20px;
}
header .container {
  max-width: 1170px;
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
}
header .container .logo {
  display: flex;
}
header .container nav {
  display: flex;
  align-items: center;
}
header .container nav ul {
  display: flex;
}
header .container nav ul li {
  margin: 0 10px;
  list-style: none;
}
header .container nav ul li a {
  font-size: 16px;
  font-weight: 500;
  color: #301566;
}

.hero {
  position: relative;
}
.hero .background-image {
  background: url("../img/1.jpg") no-repeat 58% 75%;
  background-size: cover;
  position: absolute;
  height: 100%;
  width: 100%;
  filter: brightness(80%) grayscale(20%);
  z-index: -1;
}
.hero .container {
  padding: 110px 40px 90px 40px;
  display: flex;
  flex-direction: column;
  text-align: center;
  max-width: 900px;
  margin: 0 auto;
}

.text {
  padding: 100px 20px 0 20px;
}
.text .container {
  display: flex;
  flex-direction: column;
  text-align: center;
  max-width: 725px;
  margin: 0 auto;
  padding: 20px 20px 0 20px;
}
.text .container h2 {
  margin-bottom: 20px;
}
.text .container p {
  margin-bottom: 30px;
}

.gallery {
  display: flex;
  gap: 7px;
}
.gallery img {
  width: 33.33333%;
}

.blocks {
  background-color: #EEEEEE;
  padding: 40px 20px 50px 20px;
}
.blocks .container {
  max-width: 1050px;
  display: flex;
  justify-content: center;
  margin: 0 auto;
}
.blocks .container .block {
  padding: 40px 20px;
  background-color: white;
  width: 33.33333%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.blocks .container .block .icon {
  height: 88px;
  width: 88px;
