  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
}
.blocks .container .block .icon img {
  height: 55px;
}
.blocks .container .block h4 {
  text-align: center;
  margin-bottom: 20px;
}
.blocks .container .block p {
  text-align: center;
  margin-bottom: 30px;
}

.img-right {
  padding: 60px 20px;
}
.img-right .container {
  display: flex;
  margin: 20px;
  max-width: 1170px;
  margin: 20px auto;
}
.img-right .container .content {
  width: 50%;
