  gap: 20px;
}
.img-right .container .content .buttons a {
  margin: 0;
}
.img-right .container .image {
  height: 620px;
  width: 50%;
}
.img-right .container .image img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

