}
.text-boxes .container .contact-form {
  width: 50%;
  padding: 40px;
}
.text-boxes .container .contact-form form {
  display: flex;
  flex-direction: column;
}
.text-boxes .container .contact-form form label {
  margin: 10px 0 4px 0;
  font-weight: 700;
}
.text-boxes .container .contact-form form label span {
  color: red;
  font-weight: 400;
}
.text-boxes .container .contact-form form input {
  max-width: 70%;
  height: 38px;
  margin: 10px 0;
}
.text-boxes .container .contact-form form input:focus, .text-boxes .container .contact-form form textarea:focus {
  border: 1px solid #999;
}
.text-boxes .container .contact-form form input[type=checkbox] {
  max-width: none;
  height: initial;
  display: inline;
  margin: 0;
  margin-right: 3px;
}
.text-boxes .container .contact-form form input[type=submit] {
  max-width: min-content;
  height: initial;
  display: inline;
  background-color: #EEEEEE;
  padding: 10px 15px;
  font-weight: 700;
  font-size: 16px;
}
.text-boxes .container .contact-form form textarea {
