}
.text-boxes .container .contact-form form input, .text-boxes .container .contact-form form textarea {
  border: 1px solid #ccc;
  padding: 6px 10px;
  margin-bottom: 10px;
  border-radius: 2px;
}
.text-boxes .container .contact-form form .checkboxes {
  margin-bottom: 10px;
}
.text-boxes .container .contact-form form .checkboxes label {
